html, body {
  font-family: ArialMT, Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0; }

.nav {
  cursor: pointer;
  grid-area: nav;
  margin: 0;
  padding: 0; }
  .nav_column {
    display: flex;
    flex-direction: column; }
  .nav__item {
    opacity: .6; }
    .nav__item:hover {
      opacity: 1; }
    .nav__item_horizontal {
      display: inline;
      text-decoration: none; }
    .nav__item_vertical {
      position: relative;
      list-style-type: none;
      margin: 0 0 30px;
      text-align: center; }
    .nav__item_big {
      color: #333;
      font-size: 16px;
      font-weight: bold;
      line-height: 20px;
      margin-right: 27px; }
    .nav__item_small {
      color: #858585;
      font-size: 12px;
