      margin-right: 15px; }
    .nav__item_active {
      opacity: 1; }

.main-page {
  background-color: #FFF;
  display: grid;
  grid-template-areas: "header" "events-page" "footer";
  grid-template-columns: 1fr;
  grid-template-rows: 92px 1fr 64px;
  height: 100vh;
  width: 100%; }

.header {
  align-items: center;
  border-bottom: 1px #F0F0F0 solid;
  display: grid;
  grid-area: header;
  grid-template-areas: "logo nav";
  grid-template-columns: 160px 1fr;
  height: 92px;
  padding-left: 40px;
  z-index: 99; }
  @media screen and (max-width: 910px) {
    .header {
      grid-template-areas: "logo nav expander";
      grid-template-columns: 160px 1fr 16px;
      background-color: #FFF;
      border-bottom: none;
      height: 64px;
      padding: 0 20px;
      position: fixed;
      width: calc(100% - 40px); } }
  .header__logo {
    grid-area: logo;
    margin-right: 52px; }
  @media screen and (max-width: 910px) {
    .header__nav {
      display: none; } }
  .header__expander {
    background: url(./images/icon_list_m.png) no-repeat center center;
    border: none;
    cursor: pointer;
    display: none;
    height: 16px;
    width: 16px; }
    @media screen and (max-width: 910px) {
      .header__expander {
        grid-column: 3;
        display: block; } }
    .header__expander:focus {
      border: none; }

.footer {
  align-items: center;
  border-top: 1px #F0F0F0 solid;
  display: grid;
  grid-area: footer;
  grid-template-areas: "nav copyright";
  grid-template-columns: 2fr 1fr;
  padding-left: 40px; }
  @media screen and (max-width: 910px) {
    .footer {
      display: none; } }
  .footer__copyright {
    color: #858585;
    font-size: 12px;
    justify-self: right;
    letter-spacing: 0;
    padding-right: 15px; }

.sidebar {
  display: none; }
  @media screen and (max-width: 1100px) {
    .sidebar {
      background-color: #FFF;
      display: block;
      opacity: 0;
      position: fixed;
      top: 64px;
      transition: 1s;
      visibility: hidden;
      width: 100%; } }
  .sidebar_opened {
    border-bottom: 2px solid #FFCD00;
    opacity: 1;
    visibility: visible;
    width: 100%;
    z-index: 99; }

.events-page {
  margin: 0 auto;
  max-width: 1035px;
  padding-bottom: 80px; }
  @media screen and (max-width: 1100px) {
    .events-page {
      padding: 0 40px 40px; } }
  @media screen and (max-width: 910px) {
    .events-page {
      margin-top: 45px; } }
  @media screen and (max-width: 650px) {
    .events-page {
      margin-top: 67px;
      padding: 0 20px 20px; } }
  .events-page__title {
    font-size: 36px;
    font-weight: bold;
    margin: 33px 0; }
    @media screen and (max-width: 650px) {
      .events-page__title {
