  padding: 20px;
  color: #333;
  display: flex;
  flex-direction: column; }
  .card:hover {
    background-color: #F3F3F3;
    box-shadow: 0 2px 6px 0 rgba(197, 186, 186, 0.5); }
  .card:hover .card__close {
    display: block; }
  .card:hover .card__expand {
    display: block; }
    @media screen and (max-width: 650px) {
      .card:hover .card__expand {
        display: none; } }
  @media screen and (max-width: 650px) {
    .card {
      padding: 20px 16px;
      margin-bottom: 16px; } }
  .card_s {
    grid-column: span 2; }
  .card_m {
    grid-column: span 3; }
    @media screen and (max-width: 910px) {
      .card_m {
        grid-column: span 4; } }
  .card_l {
    grid-column: span 4; }
    @media screen and (max-width: 1100px) {
      .card_l {
        grid-column: span 5; } }
    @media screen and (max-width: 910px) {
      .card_l {
        grid-column: span 4; } }
  .card_with-image {
    grid-row: span 3; }
  .card_critical {
    border-radius: 20px;
    background-color: #DB5341;
    color: #FFF; }
    .card_critical:hover {
      background-color: #DB5341; }
  .card__close {
    cursor: pointer;
    margin-left: auto;
    align-self: baseline;
    display: none; }
    @media screen and (max-width: 650px) {
      .card__close {
        width: 10px; } }
  .card__expand {
    cursor: pointer;
    margin-left: auto;
    align-self: flex-end;
    display: none;
    width: 10px;
    max-height: 36px; }
    .card__expand_absolute {
      position: absolute;
      bottom: 20px;
      right: 20px; }
  .card__row {
    display: flex; }
  .card__expander-row {
    display: flex; }
  .card__button {
    user-select: none;
    padding: 16px 53px;
    border-radius: 8px;
