    .card__basic-info_with-bottom-margin {
      margin-bottom: 20px; }
  .card__description {
    padding-top: 25px;
    font-size: 20px;
    color: #000; }
    @media screen and (max-width: 650px) {
      .card__description {
        font-size: 18px;
        padding-top: 15px; } }
    .card__description_l {
      font-size: 24px; }
      @media screen and (max-width: 650px) {
        .card__description_l {
          font-size: 18px; } }
  .card__measurements {
