  .card__details {
    margin: auto -21px -21px -21px;
    border-radius: 20px;
    color: #000;
    background-color: #FAFAFA;
    padding: 20px;
    font-size: 20px; }
    .card__details:hover {
      background-color: #F3F3F3;
      box-shadow: 0 2px 6px 0 rgba(197, 186, 186, 0.5); }
    @media screen and (max-width: 650px) {
      .card__details {
        padding: 16px 16px 20px;
        font-size: 18px; } }
  .card__last-element {
    padding-top: 20px;
    margin: auto 0 0; }
    @media screen and (max-width: 650px) {
      .card__last-element {
        padding-top: 16px; } }
  .card__image-container {
    padding-top: 30px; }
  .card__image {
    user-select: none; }
    .card__image_icon {
      max-width: 48px; }
      @media screen and (max-width: 650px) {
        .card__image_icon {
          max-width: 36px; } }
    .card__image_attachement {
      border-radius: 20px;
      height: 100%;
      width: 100%; }
    .card__image_camera {
      background: url(./images/bitmap.png) repeat-x center center;
      background-position-x: -100px;
      border-radius: 20px;
      height: 424px;
      width: 100%;
      background-size: 830px; }
      @media screen and (max-width: 650px) {
        .card__image_camera {
          height: 250px;
          background-size: 500px;
          background-position-x: 500px;
          filter: brightness(100%); } }
  .card__camera-interface {
