    z-index: 10; }

.player {
  display: grid;
  grid-template-columns: 70px 70px 1fr 50px;
  grid-template-rows: 26px 20px 26px 52px;
  grid-template-areas: "logo name name name" "logo time time t-val" "gap  gap  gap  gap" "prev next vol  v-val"; }
  @media screen and (max-width: 650px) {
    .player {
      grid-template-columns: 65px 65px 1fr 45px; } }
  .player__logo {
    grid-area: logo;
    user-select: none;
    max-width: 52px;
    border-radius: 5px; }
  .player__name {
    grid-area: name;
    color: #000;
    font-size: 20px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; }
    @media screen and (max-width: 650px) {
      .player__name {
        font-size: 18px; } }
  .player__button {
    cursor: pointer;
    user-select: none; }
    .player__button_prev {
      grid-area: prev;
      background: url(./images/prev.svg) no-repeat left center; }
    .player__button_next {
      grid-area: next;
      background: url(./images/prev.svg) no-repeat left center;
      transform: rotate(180deg); }
  .player__value {
    align-self: center;
    justify-self: right;
    font-size: 14px;
    color: rgba(161, 161, 161, 0.84);
    user-select: none; }
    .player__value_time {
      margin-top: 7px;
      grid-area: t-val; }
    .player__value_volume {
      grid-area: v-val; }
  .player__time {
    grid-area: time;
    align-self: center;
    outline: none;
    -webkit-appearance: none;
    width: 100%;
    background: transparent; }
    .player__time::-webkit-slider-thumb {
      margin-top: -9px;
      -webkit-appearance: none;
      height: 20px;
      width: 12px;
      border-radius: 4.5px;
      background: #D8D8D8;
      cursor: pointer; }
    .player__time::-moz-range-thumb {
      height: 20px;
      width: 9px;
      border-radius: 4.5px;
      background: #D8D8D8;
      cursor: pointer; }
    .player__time::-ms-thumb {
      margin-top: 3px;
      height: 20px;
      width: 9px;
      border-radius: 4.5px;
      background: #D8D8D8;
      cursor: pointer; }
    .player__time::-webkit-slider-runnable-track {
      margin-top: 14px;
      width: 100%;
      height: 1px;
      cursor: pointer;
      background: #979797; }
    .player__time::-moz-range-track {
      width: 100%;
      height: 1px;
      cursor: pointer; }
    .player__time::-ms-track {
      margin-top: 12px;
      width: 100%;
      height: 0;
      cursor: pointer;
      background: #979797; }
  .player__volume {
    margin-left: 15px;
    grid-area: vol;
    align-self: center;
    outline: none;
    -webkit-appearance: none;
    width: 100%;
    background: transparent; }
    .player__volume::-webkit-slider-thumb {
      -webkit-appearance: none;
      height: 16px;
      width: 16px;
      border-radius: 8px;
      background: #605757;
      cursor: pointer;
      margin-top: -7px; }
    .player__volume::-moz-range-thumb {
      height: 16px;
      width: 16px;
      border-radius: 8px;
      background: #605757;
      cursor: pointer; }
    .player__volume::-ms-thumb {
      margin-top: -1px;
      height: 16px;
      width: 16px;
      border-radius: 8px;
      background: #605757;
      cursor: pointer; }
    .player__volume::-webkit-slider-runnable-track {
      width: 100%;
      height: 2px;
      cursor: pointer;
      background: rgba(216, 216, 216, 0.3); }
    .player__volume::-moz-range-track {
      width: 100%;
      height: 2px;
      cursor: pointer;
      background: rgba(216, 216, 216, 0.3); }
    .player__volume::-ms-track {
      width: 100%;
      height: 2px;
      cursor: pointer;
      background: rgba(216, 216, 216, 0.3); }

.measurement {
  display: flex; }
  .measurement__value {
    font-weight: bold; }

.video-page {
  margin: 100px auto 60px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 230px);
  grid-gap: 20px;
  align-items: center; }
  .video-page__video {
    max-width: 300px;
    transition: transform .3s; }
    .video-page__video_opened {
      transition: transform .3s;
      transform-origin: center center;
      z-index: 10; }

.video-controls {
