    outline: none;
    -webkit-appearance: none;
    width: 100%;
    background: transparent;
    margin-top: 0; }
    .video-controls__input::-webkit-slider-thumb {
      -webkit-appearance: none;
      height: 16px;
      width: 16px;
      border-radius: 8px;
      background: #605757;
      cursor: pointer;
      margin-top: -5px; }
    .video-controls__input::-moz-range-thumb {
      height: 16px;
      width: 16px;
      border-radius: 8px;
      background: #605757;
      cursor: pointer; }
    .video-controls__input::-ms-thumb {
      margin-top: -1px;
      height: 16px;
      width: 16px;
      border-radius: 8px;
      background: #605757;
      cursor: pointer; }
    .video-controls__input::-webkit-slider-runnable-track {
      width: 100%;
      height: 5px;
      cursor: pointer;
      background: rgba(216, 216, 216, 0.3); }
    .video-controls__input::-moz-range-track {
      width: 100%;
      height: 2px;
      cursor: pointer;
      background: rgba(216, 216, 216, 0.3); }
    .video-controls__input::-ms-track {
      width: 100%;
      height: 2px;
      cursor: pointer;
      background: rgba(216, 216, 216, 0.3); }
